<template>
  <div class="work-page">
    <div class="breadcrumb">
      <typewriter typeSpeed="50" :phrases="breadcrumb" numOfLoops="1"></typewriter>
    </div>
    <header class="page-header">
      <h1 class="title">FREELANCE WORK</h1>
      <div class="desc">Some of my freelance works</div>
    </header>
    <div class="page-container">
      <div ref="workList" class="work-list">
        <isotope ref="cpt" v-images-loaded:on.progress="layout" :options="isotopeOpt" :list="works">
          <div v-for="(el,index) in works" :key="index" @click="selected=el">
            <a
              target="_blank"
              :href="el.url"
              :class="['inner', {'disabled': getStatus(el.status) !== 'online'}]"
            >
              <div class="image-wrapper">
                <img :src="imagePath(el.image)" :alt="`${el.name} project`" />
                <div class="overlay-info">
                  <div class="info">
                    <div class="desc" v-html="el.description">
                      <!--em>Welovewebs.com</em-->
                    </div>
                  </div>
                </div>
              </div>
              <div class="caption">
                <div>
                  <span>{{el.name}}</span>
                </div>
                <div
                  :class="['status', getStatus(el.status).replace(/\s/g, '')]"
                >{{getStatus(el.status)}}</div>
              </div>
            </a>
          </div>
        </isotope>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
import isotope from "vueisotope";
import Typewriter from "@/components/TypeWriter.vue";
import imagesLoaded from "vue-images-loaded";
import db from "@/main";

export default {
  name: "work",
  components: { isotope, Typewriter },
  data() {
    return {
      breadcrumb: [":~ Init > freelance works"],
      works: [],
      selected: null,
      isotopeOpt: {
        getSortData: {
          id: "id"
        },
        sortBy: "id"
      }
    };
  },
  created() {
    this.fetchData();
  },
  computed: {
    ...mapState(["user"])
  },
  directives: {
    imagesLoaded
  },
  methods: {
    fetchData() {
      db.collection("work")
        .orderBy("order", "desc")
        .get()
        .then(snapshot => {
          snapshot.forEach(w => {
            this.works.push(w.data());
          });
        });
    },
    layout() {
      this.$refs.cpt.layout("masonry");
    },
    imagePath: function(imgName) {
      return require("../assets/" + imgName);
    },
    getStatus(s) {
      if (s === 0) return "online";
      else if (s === 1) return "offline";
      else if (s === 2) return "deprecated";
      else return "in progress";
    }
  }
};
</script>
<style lang="scss" scoped>
.work-page {
  position: relative;
  padding-top: 6rem;
  @media screen and (max-width: 767px) {
    padding-top: 1rem;
  }
  .page-container {
    max-width: 1140px;
  }
  .item {
    width: 33.333333%;
    //width: 50%;
    margin-bottom: 2rem;
    opacity: 0.9;
    @media screen and (max-width: 1199px) {
      width: 50%;
    }
    @media screen and (max-width: 767px) {
      width: 100%;
    }
    .inner {
      padding: 1rem;
      display: block;
      &.disabled {
        pointer-events: none;
      }
      .image-wrapper {
        margin-bottom: 2rem;
        position: relative;
        img {
          width: 100%;
          -webkit-box-shadow: 0px 9px 17px -10px rgba(0, 0, 0, 0.75);
          -moz-box-shadow: 0px 9px 17px -10px rgba(0, 0, 0, 0.75);
          box-shadow: 0px 9px 17px -10px rgba(0, 0, 0, 0.75);
        }
        .overlay-info {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%) scale(0.9);
          width: 7rem;
          height: 9rem;
          opacity: 0;
          background-color: $main-color;
          transition: $g-trans;
          .info {
            position: absolute;
            color: white;
            text-align: center;
            padding: 1rem;
            top: 50%;
            width: 100%;
            font-size: 80px;
            -ms-transform: translateY(-50%);
            transform: translateY(-50%);
            opacity: 0;

            .desc {
              font-size: 1rem;
              line-height: 1.5;
            }
          }
        }
      }
    }
    &:hover {
      opacity: 1;
      .inner {
        .overlay-info {
          width: 100%;
          height: 100%;
          opacity: 0.9;
          .info {
            opacity: 1;
            transition: opacity 400ms 300ms;
          }
        }
        .image-wrapper {
          img {
          }
        }
      }
    }

    .caption {
      text-align: center;
      .status {
        display: inline-block;
        background-color: #84ff84;
        padding: 0.5rem;
        font-size: 0.9rem;
        margin-top: 10px;
        &.online {
          background-color: #84ff84;
        }
        &.offline {
          background-color: #ff9595;
        }
        &.deprecated {
          background-color: #ffcf76;
        }
        &.inprogress {
          background-color: #d4d4ff;
        }
      }
    }
  }

  .isoDefault {
    min-height: 210px;
  }
}
</style>